
#app html,body,h1,h2,h3,h4,h5 {
  font-family: 'Raleway', sans-serif;
}
tbody tr {
  height: 80px !important;
}
tfoot tr {
  height: 60px !important;
  font-weight: bold;
}
td {
  vertical-align: middle !important;
}
textarea {
  resize: vertical;
}
.w3-modal {
  padding-top: 30px;
  display: block;
}
.w3-modal-content {
  margin-top: 50px;
  margin-bottom: 50px;
  border-radius: 20px;
  min-height: 200px;
}
.w3-modal-content .w3-card-4 {
  border-radius: 20px;
  min-height: 200px;
}
.white-bg {
  background-color: white;
}
.dark-gray {
  background-color: #313942;
  color: white;
}
.dark-gray-selected {
  background-color: #3e464e;
  color: white;
}
.gray-1 {
  background-color: #637484;
  color: white;
}
.gray-1-color {
  color: #637484;
}
.gray-2 {
  background-color: #6f8294;
}
.gray-2-color {
  color: #6f8294;
}
.light-grey {
  background-color: #eff3f6;
  color: black;
}
.white-text {
  color: white;
}
.border-blue {
  border-color: #15a5cc !important;
}
.app-blue {
  background-color: #95bad6 !important;
  color: #313942;
}
.app-button {
  background-color: #15a5cc !important;
  color: white;
  border-radius: 12px;
}
.app-button-light {
  background-color: #f1f1f1 !important;
  color: black;
  border-radius: 12px;
}
.app-button-danger {
  background-color: #f1cdcd !important;
  color: black;
  border-radius: 12px;
}
.black-text {
  color: black;
}
.success-panel {
  color: white;
  background-color:  rgb(64, 163, 109) !important;
}
.warning-panel {
  color: white;
  background-color:  rgb(194, 157, 0) !important;
}
.error-row {
  margin-top: 10px;
  width: 100%;
}
.error-panel {
  color: black;
  background-color: #f1cdcd;
  margin-bottom: 6px;
}
.error-text {
  color: rgb(255, 64, 64);
}
.error-input {
  outline: none;
  border-color: #f1cdcd !important;
  box-shadow: 0 0 10px red;
}
.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}
.flex-vert {
  display: -webkit-box !important;
  display: -ms-flexbox !important;
  display: flex !important;
  -webkit-box-pack: center !important;
      -ms-flex-pack: center !important;
          justify-content: center !important;
  -webkit-box-orient: vertical !important;
  -webkit-box-direction: normal !important;
      -ms-flex-direction: column !important;
          flex-direction: column !important;
}
.slider-container {
  overflow: hidden;
}
.cursor-pointer {
  cursor: pointer !important;
}
.open-color {
  background-color: #32a11b;
}
.done-color {
  background-color: #b8b8b8;
}
.revert-color {
  background-color: #ff4b4b;
}
.section-header {
  text-align: left;
  margin-left: 16px;
}

/* Animations */
.fadeenter-enter-active {
  transition: opacity .9s
}
.fadeenter-enter {
  opacity: 0
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .9s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in <2.1.8 */ {
  opacity: 0
}
.slideToRight-enter-active {
  -webkit-animation: slideToRight-in 0.5s ease forwards;
          animation: slideToRight-in 0.5s ease forwards;
}
.slideToRight-leave-active {
  -webkit-animation: slideToRight-out 0.5s ease forwards;
          animation: slideToRight-out 0.5s ease forwards;
}
.slideToLeft-enter-active {
  -webkit-animation: slideToLeft-in 0.5s ease forwards;
          animation: slideToLeft-in 0.5s ease forwards;
}
.slideToLeft-leave-active {
  -webkit-animation: slideToLeft-out 0.5s ease forwards;
          animation: slideToLeft-out 0.5s ease forwards;
}
.slideToUp-enter-active {
  -webkit-animation: slideToUp-in 0.5s ease forwards;
          animation: slideToUp-in 0.5s ease forwards;
}
.slideToUp-leave-active {
  -webkit-animation: slideToUp-out 0.5s ease forwards;
          animation: slideToUp-out 0.5s ease forwards;
}
.slideToDown-enter-active {
  -webkit-animation: slideToDown-in 0.5s ease forwards;
          animation: slideToDown-in 0.5s ease forwards;
}
.slideToDown-leave-active {
  -webkit-animation: slideToDown-out 0.5s ease forwards;
          animation: slideToDown-out 0.5s ease forwards;
}
.slideDownUp-enter-active {
  -webkit-animation: slideToDown-in 0.5s ease forwards;
          animation: slideToDown-in 0.5s ease forwards;
}
.slideDownUp-leave-active {
  -webkit-animation: slideToUp-out 0.5s ease forwards;
          animation: slideToUp-out 0.5s ease forwards;
}
.slideRightLeft-enter-active {
  -webkit-animation: slideToRight-in 0.5s ease forwards;
          animation: slideToRight-in 0.5s ease forwards;
}
.slideRightLeft-leave-active {
  -webkit-animation: slideToLeft-out 0.5s ease forwards;
          animation: slideToLeft-out 0.5s ease forwards;
}
@-webkit-keyframes slideToRight-in {
from {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@keyframes slideToRight-in {
from {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@-webkit-keyframes slideToRight-out {
from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
to {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
}
@keyframes slideToRight-out {
from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
to {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
}
@-webkit-keyframes slideToLeft-in {
from {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@keyframes slideToLeft-in {
from {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@-webkit-keyframes slideToLeft-out {
from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
to {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
}
@keyframes slideToLeft-out {
from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
to {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
}
@-webkit-keyframes slideToDown-in {
from {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}
to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
@keyframes slideToDown-in {
from {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}
to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
@-webkit-keyframes slideToDown-out {
from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
to {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
}
}
@keyframes slideToDown-out {
from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
to {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
}
}
@-webkit-keyframes slideToUp-in {
from {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
}
to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
@keyframes slideToUp-in {
from {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
}
to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
@-webkit-keyframes slideToUp-out {
from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
to {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}
}
@keyframes slideToUp-out {
from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
to {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
}
}




